import React,{useState} from 'react'
import config from '../../config'

export default () => {
    const [show,setShow] = useState(false);
    return (
        <div className="flex flex-wrap justify-between py-4 items-center">
            <a href="/" className="">
                <div className="text-4xl text-cryp-text font-bold text-cryp-hover flex items-center">
            <img src="/static/icons/icon-128x128.png" className="w-12 h-12 mr-4"/>
                <div className="text-center">
                    <div className="">CryptoFair</div>
                    <div className="text-sm text-red-400">Share more - Get more</div>
                </div>
            </div>
            </a>
            <div className="lg:hidden w-6 h-6">
                <button className="w-6 h-6 focus:outline-none" onClick={()=>setShow(!show)}>
                    {!show?(<svg viewBox="0 0 448 512" className="w-full h-full fill-current text-cryp-hover">
                        <path fill="currentColor" d="M16 132h416c8.837 0 16-7.163 16-16V76c0-8.837-7.163-16-16-16H16C7.163 60 0 67.163 0 76v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16z"></path>
                    </svg>):(<svg viewBox="0 0 352 512" className="w-full h-full fill-current text-cryp-hover">
                        <path fill="currentColor" d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"></path>
                    </svg>)}
                </button>
            </div>
            <div className={`lg:block w-full md:w-auto text-right ${show?null:'hidden'}`}>
                <a href="/howitworks" className="block md:inline-block text-lg text-cryp-text hover:text-cryp-hover mb-2 mr-6">How it works</a>
                <a href="/faq" className="block md:inline-block text-lg text-cryp-text hover:text-cryp-hover mb-2 mr-6">FAQ</a>
                <a href="/faqeth" className="block md:inline-block text-lg text-cryp-text hover:text-cryp-hover mb-2 mr-6">Ethereum FAQ</a>
                <a href={config.signup} className="inline-block text-lg w-32 text-center py-2 bg-cryp-hover rounded-full text-white border border-cryp-hover hover:bg-cryp-bold hover:border-cryp-bold mr-6">Sign Up</a>
                <a href={config.login} className="inline-block text-lg w-32 text-center py-2 border border-cryp-hover rounded-full text-cryp-hover hover:border-cryp-bold hover:text-cryp-bold">Login</a>
            </div>                    
        </div>     
    )
}